import { useContext } from "react";
import { MarineContext } from "../MarinePolicy";
import { TextField, Divider, Button } from "@mui/material";

export default function MarinePolicyPremium() {
  const { state, handleInputChange, data, computation, isAddOrEditMode } =
    useContext(MarineContext);

  return (
    <div style={{ display: "flex" }}>
      <div
        style={{
          display: "flex",
          columnGap: "10px",
          flex: 1,
          flexDirection: "column",
        }}
      >
        <fieldset
          style={{
            display: "flex",
            flexDirection: "column",
            rowGap: "10px",
            padding: "15px",
            border: "1px solid #cbd5e1",
            borderRadius: "5px",
            flex: 1,
          }}
        >
          <legend>
            Clauses, Endorsements, Special Conditions and Warranties
          </legend>
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            name="prem_text_one"
            value={state.prem_text_one}
            multiline
            rows={5}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            name="prem_text_two"
            value={state.prem_text_two}
            multiline
            rows={5}
            onChange={handleInputChange}
          />
        </fieldset>
      </div>
      <fieldset
        style={{
          display: "flex",
          flexDirection: "column",
          rowGap: "10px",
          padding: "15px",
          border: "1px solid #cbd5e1",
          borderRadius: "5px",
        }}
      >
        <legend>Premiums</legend>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            padding: "5px",
          }}
        >
          <Button
            disabled={isAddOrEditMode}
            size="small"
            variant="contained"
            color="primary"
            onClick={() => {
              computation();
            }}
          >
            Compute
          </Button>
        </div>

        <div
          style={{
            display: "grid",
            gridTemplateColumns: "repeat(2,1fr)",
            gap: "10px",
          }}
        >
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Insured Value"
            name="insuredValue"
            value={state.insuredValue}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Percentage"
            name="percentagePremium"
            value={state.percentagePremium}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Total Premium"
            name="totalPremium"
            value={state.totalPremium}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Vat"
            name="vat"
            value={state.vat}
            onChange={handleInputChange}
          />
          <TextField
            disabled={isAddOrEditMode}
            required
            variant="outlined"
            size="small"
            label="Doc Stamp"
            name="docStamp"
            value={state.docStamp}
            onChange={handleInputChange}
          />
          <div style={{ display: "flex", gap: "5px", position: "relative" }}>
            <TextField
              sx={{ width: "80px" }}
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              name="localGovTaxPercent"
              value={state.localGovTaxPercent}
              onChange={handleInputChange}
            />
            <TextField
              disabled={isAddOrEditMode}
              required
              variant="outlined"
              size="small"
              label="Local Gov Tax"
              name="localGovTax"
              value={state.localGovTax}
              onChange={handleInputChange}
            />
          </div>
        </div>
        <Divider color="secondary" />
        <TextField
          disabled={isAddOrEditMode}
          required
          variant="outlined"
          size="small"
          label="Total Due"
          name="totalDue"
          value={state.totalDue}
          onChange={handleInputChange}
        />
      </fieldset>
    </div>
  );
}
